import React, { Component } from 'react';

class Event extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filteredevents: [],
            showDetails: false,
        }
    }
    // detailShow() {
    //     console.log('clicked: ' + this.props.event.name)
    //     this.setState({ showDetails: true })
    //     const theId = this.props.event.id
    //     this.setState({selectedEventId: theId})
    // }
    detailHide() {
        this.setState({ showDetails:false })
    }

    getData = () => {
        const rsvped = this.props.event.yes_rsvp_count;
        const max = this.props.event.rsvp_limit;
        const openings = max - rsvped

        return (
            [
                {"name": "Reserved", "value": rsvped},
                {"name": "Spots Left", "value": openings}
            ]
        )
    }
    makeModal(theId) {
        this.props.logFunc(theId)
    }
    render(){
        return(
            <div className="event" onClick = {() => this.makeModal(this.props.event.id)}>
                <h2 className="event-name">{this.props.event.name}</h2>                
            </div>
        )
    }
}
export default Event;